@import "../../styles/item_attributes.scss";

.footer-container {
    background: #0e1b3deb;
    display: flex;
    width: 100%;
    height: 3.75rem;
    .footer-button {
        display: flex;
        flex: 1;
        flex-direction: column;
    }
    .footer-button {
        zoom: 1.2;
        background-color: #0e1b3deb !important;
        margin-inline: unset;
        letter-spacing: 0.63px;
        font-family: "Dubai";
        --color: white;
    }
    .button {
        font-size: 2rem;
    }
    ion-label {
        font-size: 8px;
    }
}
